/* PFDinTextPro */
@font-face {
  font-family: "PFDinTextPro";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PFDinTextPro"), url("../fonts/PFDinTextPro.woff") format("woff");
}

html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  background-color: #fbfbfb;
  font-family: "PFDinTextPro", sans-serif;
  font-size: 18px;
  /* line-height: ; */
  color: #000000;
}
body.modal-active {
  overflow: hidden;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
  color: inherit;
}
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
.clearfix::after {
  content: "";
  display: table;
  clear: both;
}
.fix-container {
  max-width: 1200px;
  margin: 0 auto;
}
.hidden {
  display: none;
}
