  font-family: "PFDinTextPro";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PFDinTextPro"), url("../fonts/PFDinTextPro.woff") format("woff");
}
.first-section {
  padding-top: 270px;
  padding-bottom: 208px;
  background-color: #bad2e7;
  background-image: url("../img/bg1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 768px) {
  .first-section {
    display: none;
  }
}
.first-section__heading {
  margin: 0 auto 85px auto;
  text-align: center;
  font-size: 48px;
  font-weight: 300;
}
.second-section {
  background-image: url("../img/bg2.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  background-clip: border-box;
  padding-bottom: 250px;
}
@media (max-height: 1024px) {
  .second-section {
    background-position: center;
  }
}
@media (max-width: 768px) {
  .second-section {
    padding-top: 60px;
    background-image: url("../img/bg1.jpg");
  }
  .second-section_bg-off {
    background-image: none;
    background-color: #ededd5;
  }
}
.second-section__heading {
  margin: 0 auto 24px auto;
  max-width: 260px;
  font-size: 36px;
  font-weight: 300;
  text-align: center;
}
.menu__list {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu__link {
  display: block;
  text-align: center;
  width: 345px;
  height: 345px;
  padding-top: 203px;
  border-radius: 50%;
  box-shadow: 0 0 0 9px rgba(255, 255, 255, 0.6);
  background-color: rgba(255, 255, 255, 0.6);
  background-repeat: no-repeat;
  background-position: center bottom 184px;
  font-size: 28px;
  color: #242424;
}
@media (max-width: 1140px) {
  .menu__link {
    width: 250px;
    height: 250px;
    padding-top: 140px;
    background-position: center bottom 135px;
  }
}
@media (max-width: 900px) {
  .menu__link {
    width: 220px;
    height: 220px;
    padding-top: 126px;
    background-position: center bottom 105px;
  }
}
.menu__link_trade {
  background-image: url("../img/icon1.png");
}
.menu__link_invest {
  background-image: url("../img/icon2.png");
}
.menu__link_invite {
  background-image: url("../img/icon3.png");
}
.menu__link:hover,
.menu__link:active {
  box-shadow: inset 0 0 0 1px #00b0a2, 0 0 0 9px rgba(255, 255, 255, 0.6);
}
.tabs__menu {
  border-bottom: 1px solid #c0c1ba;
}
@media (max-width: 768px) {
  .tabs__menu {
    display: none;
  }
}
.tabs__link-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  height: 115px;
  list-style: none;
}
.tabs__link-list-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33.33%;
  height: 100%;
}
.tabs__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  height: 100%;
  font-size: 28px;
  font-weight: 400;
  border-bottom: 3px solid transparent;
}
.tabs__link_active,
.tabs__link:hover,
.tabs__link:active {
  border-bottom: 3px solid #009e82;
  color: #009e82;
}
.tabs__container {
  display: none;
  padding-top: 86px;
}
@media (max-width: 768px) {
  .tabs__container {
    display: block;
    padding-top: 0;
    padding-right: 50px;
    padding-left: 50px;
    margin-bottom: 13px;
  }
}
.tabs__container_active {
  display: block;
}
@media (max-width: 768px) {
  .tabs__container_active {
    padding-bottom: 271px;
    margin-bottom: 25px;
    background-image: url("../img/bg2.png");
    background-position: bottom right;
    background-size: contain;
    background-repeat: no-repeat;
  }
}
.tabs__text {
  margin: 0 0 47px 0;
  max-width: 680px;
  line-height: 24px;
}
@media (max-width: 768px) {
  .tabs__text {
    margin: 0 0 31px 0;
    font-size: 16px;
    line-height: 1;
  }
}
.tabs__heading {
  margin: 0 0 35px 0;
  font-size: 24px;
  font-weight: 300;
}
@media (max-width: 768px) {
  .tabs__heading {
    margin: 0 0 30px 0;
  }
}
.tabs__steps-list {
  counter-reset: myCounter;
  margin-bottom: 35px;
}
.tabs__steps-item {
  display: flex;
  align-items: center;
  max-width: 625px;
  list-style: none;
}
.tabs__steps-item:not(:last-of-type) {
  margin-bottom: 10px;
}
.tabs__steps-item::before {
  counter-increment: myCounter;
  content: counter(myCounter);
  margin-right: 20px;
  font-size: 32px;
  color: #00b596;
  font-weight: 300;
}
.tabs__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 156px;
  height: 45px;
  border-radius: 3px;
  background-color: #009e82;
  color: #feffff;
  font-weight: 500;
}
.tabs__accordion {
  display: none;
  width: 100%;
  height: 69px;
  margin: 0;
  padding-left: 75px;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.6);
  background-repeat: no-repeat;
  background-position: 28px center;
  background-size: 35px auto;
  color: #2B2B2B;
  font-size: 18px;
  cursor: pointer;
}
@media (max-width: 768px) {
  .tabs__accordion {
    display: flex;
  }
}
.tabs__accordion_tab1 {
  background-image: url("../img/icon1.png");
}
.tabs__accordion_tab2 {
  background-image: url("../img/icon2.png");
}
.tabs__accordion_tab3 {
  background-image: url("../img/icon3.png");
}
.tabs__accordion::after {
  content: "";
  width: 21px;
  height: 11px;
  margin-right: 15px;
  margin-left: auto;
  background-image: url("../img/arrow.png");
  background-repeat: no-repeat;
  background-position: center;
}
.tabs__accordion_active::after {
  transform: rotate(180deg);
}
.tabs__content {
  padding-top: 23px;
}
@media (max-width: 768px) {
  .tabs__content {
    display: none;
  }
  .tabs__content_active {
    display: block;
  }
}

